import React from 'react';

const NetworkThreeWayHandshake = (props) => {
    return (
        <>
            <div className="common_style">
                <span className="lblock">
                    {/*  */}
                    <span className="mblock">
                        <details>
                            <summary className="stitle">  3Way Handshake? <a name="3Way Handshake?" style={{ visibility: "hidden" }}> 3Way Handshake? </a> </summary>
                            <span className="sblock">
                                <span className="sstitle">             </span>
                                <span className="mblock">
                                    <li> TCP         , TCP  </li>
                                </span>
                            </span>
                        </details>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <details>
                            <summary className="stitle">  3Way Handshake   <a name="3Way Handshake  " style={{ visibility: "hidden" }}> 3Way Handshake   </a> </summary>
                            <span className="sblock">
                                <span className="sstitle">  </span>
                                <span className="mblock">
                                    <li> 1.    <br />  SYN   SYN/ACK  SYN_SENT   </li>
                                    <li> 1-1.    <br />   LISTEN ,  SYN   , SYN flag    </li>
                                    <li> 2.    <br /> ACK SYN ,  SYN_RECEIVED  </li>
                                    <li> 3.    <br />  ACK  ,  ESTABLISHED  </li> <br />
                                    <li> 3-1.   <br />  ESTABLISHE  </li> <br />
                                    <li> TCP :  </li>
                                    <li> SYN(Synchronize Sequence Numbers) :    </li>
                                    <li> ACK(Acknowledgment) : </li>
                                </span>
                            </span>
                        </details>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <details>
                            <summary className="stitle">  <a name="" style={{ visibility: "hidden" }}>  </a> </summary>
                            <span className="sblock">
                                <span className="sstitle">  </span>
                                <span className="mblock">
                                    <li>  </li>
                                </span>
                            </span>
                        </details>
                    </span>
                    {/*  */}
                </span>
            </div>
        </>
    );
}

export default NetworkThreeWayHandshake;