import React from 'react';

const JavaScriptAttribute = (props) => {
    return (
        <>

            <div className="common_style" >

                <div className='block1'>
                    <details>
                        <summary>    </summary>
                        <div className='block2'>

                            <h2 className='h2'>     </h2>
                            <li> ELEMENT.attributes[0] :  0    </li>
                            <li> ELEMENT.attributes.item(0) :  0    </li>
                            <li> ELEMENT.attributes.getNamedItem("ATTRIBUTE_NAME") :       </li>

                            <h2 className='h2'>     </h2>
                            <li> ELEMENT.attributes[0].name :  0    </li>
                            <li> ELEMENT.attributes.item(0).name :  0    </li>
                            <li> ELEMENT.attributes.item(0).nodeName :  0    </li>

                            <h2 className='h2'>      </h2>
                            <li> ELEMENT.attributes[0].value :  0     </li>
                            <li> ELEMENT.attributes.item(0).value :  0     </li>

                            <h2 className='h2'>   </h2>
                            <li> ELEMENT.attributes.length :     </li>
                            <li> ELEMENT.removeNamedItem("ATTRIBUTE_NAME"); </li>

                            <h2 className='h2'>       </h2>
                            <li> var x = document.createAttribute("ATTRIBUTE_NAME"); </li>
                            <li> x.value = "ATTRIBUTE_VALUE"; </li>
                            <li> ELEMENT.attributes.setNamedItem(x); </li>
                            <li>  </li>

                            <h2 className='h2'>   </h2>
                            <li>  </li>

                            <h2 className='h2'>   </h2>
                            <li> 
                                <div className='block3'>
                                    <h3 className='h3'>  </h3>
                                    <div className='block4'>
                                        <li>  </li>
                                    </div>
                                </div>
                            </li>

                        </div>
                    </details>
                </div>

            </div>
        </>
    );
}

export default JavaScriptAttribute;