import React, { useEffect, useRef } from 'react';

const SpringThymeleaf = (props) => {

    return (
        <>
            <div className="common_style">

                <div className='block1'>
                    <details>
                        <summary> thymeleaf </summary>
                        <div className='block2'>

                            <h2 className="h2">   </h2>
                            <li> thymeleaf  : https://www.thymeleaf.org/ </li>
                            <li> <a href="https://www.thymeleaf.org/doc/tutorials/3.0/usingthymeleaf.html#standard-expression-syntax" target="_blank">    thymeleaf   </a> </li>
                            <li>  : https://docs.spring.io/spring-boot/docs/2.3.1.RELEASE/reference/
                                html/spring-boot-features.html#boot-features-spring-mvc-template-engines </li>
                            <li> spring    </li>

                        </div>
                    </details>
                </div>

                <div className='block1'>
                    <details>
                        <summary> thymeleaf   </summary>
                        <div className='block2'>

                            <h2 className="h2">   </h2>
                            <li>  
                                <div className='block3'>
                                    <ul style={{ listStyle: 'none' }}>
                                        <li> <h3 className="h3">  </h3>
                                            <li> {'<!DOCTYPE html>'} </li>
                                            <li> {'<html lang="UTF-8" xmlns:th="http://www.thymeleaf.org">'} </li>
                                            <li> {'<head>'}
                                                <li> {'	<meta charset="UTF-8">'} </li>
                                                <li> {'	<meta http-equiv="X-UA-Compatible" content="IE=edge">'} </li>
                                                <li> {'	<meta name="viewport" content="width=device-width, initial-scale=1.0">'} </li>
                                                <li> {'	<title>  </title>'} </li>
                                            </li>
                                            <li> {'</head>'} </li>
                                            <li> {'<body>'} </li>
                                            <li> {'</body>'} </li>
                                            <li> {'</html>'} </li>
                                        </li> <br />
                                    </ul>
                                </div>
                            </li>

                        </div>
                    </details>
                </div>

                <div className='block1'>
                    <details>
                        <summary> thymeleaf live server  (FW.Intellij) </summary>
                        <div className='block2'>

                            <h2 className="h2">   </h2>
                            <li> Gradle compileOnly "org.springframework.boot:spring-boot-devtools"  </li>
                            <li> application.properties  application.yml  
                                <div className="block3">
                                    <li> spring.devtools.livereload.enabled=true  </li>
                                    <li> spring.resources.static-locations=file:src/main/resources/static/  </li>
                                    <li> spring.thymeleaf.cache=false  </li>
                                    <li> spring.thymeleaf.prefix=file:src/main/resources/templates/  </li>
                                </div>
                            </li>
                            <li> Ctrl + Shift + A - regi  Registry... -
                                compiler.automake.allow.when.app.running 
                            </li>
                            <li> Ctrl + Alt + S - Build, Execution, Deployment - Compiler
                                - Build project automatically   </li>
                            <li> <a href="https://chrome.google.com/webstore/detail/livereload/jnihajbhpnppcggbcgedagnkighmdlei"
                                target="_blank" rel="noopener noreferrer"> Browser Extension Live Reload  </a>  </li>
                            <li>   :         </li>

                        </div>
                    </details>
                </div>

                <div className='block1'>
                    <details>
                        <summary> thymeleaf  (  ) </summary>
                        <div className='block2'>

                            <h2 className="h2">    ( ) </h2>
                            <li> {' ${} '} #   </li>
                            <li> {' *{}'} #   
                                <div className='block3'>
                                    <li> {'<div th:object="${user}">'}
                                        <li> {'<p th:text="*{name}"> </p>'} </li>
                                        <li> {'<p th:text="*{password}"> </p>'} </li>
                                    </li>
                                    <li> {'</div>'} </li>
                                </div>
                            </li>
                            <li> {' #{} '} #   </li>
                            <li> {' @{} '} # URL  </li>
                            <li> {' ~{} '} </li>
                            <li>  : x = f() ? f() : g()    x = f() ?: g() </li>
                            <li> {'${param.name} '} <small> html name=""      </small>  </li>

                            <h2 className="h2">   {'&'}  </h2>
                            <li> 
                                <div className='block3'>
                                    <li> <h3 className="h3"> th:text th:utext </h3>
                                        <li>  th:text :   html      </li>
                                        <li>  th:utext :   html    html  </li>
                                        <li> {' <div th:text="${}"> </div> '} </li>
                                        <li> {' <div th:utext="${}"> </div> '} </li>
                                        <div className="block4">
                                            <li> {' <div th:text="${user.id}"> </div> '} <small> # user  id   </small> </li>
                                        </div>
                                    </li> <br />

                                    <li> <h3 className="h3"> th:value () </h3>
                                        <li> th:value : input    , +      </li>
                                        <li> {' <div th:value="${}+'}''{'+${}"> </div> '} </li>
                                        <div className="block4">
                                            <li>  </li>
                                        </div>
                                    </li> <br />

                                    <li> <h3 className="h3"> th:with (with       ) </h3>
                                        <li>    ,     text  </li>
                                        <li> {' <div th:with="=${}" th:text="${}"> </div> '} </li>
                                        <div className="block4">
                                            <li> {' <div th:with="name=${user.name}" th:text="${name}"> </div> '}  </li>
                                        </div>
                                    </li> <br />

                                    <li> <h3 className="h3"> th:switch , th:case (      ) </h3>
                                        <li> switch - case   </li>
                                        <li> {' <div th:switch="${}"> </div> '}
                                            <li> {' <div th:case="'}''{'"> </div> '} </li>
                                            <li> {' <div th:case="'}''{'"> </div> '} </li>
                                            <li> {' <div th:case="*"> </div> '} #    </li>
                                        </li>
                                        <li> {'</div>'} </li>
                                        <div className="block4">
                                            <li> {' <div th:switch="${user.name}"> </div> '}
                                                <li> {' <div th:case="'}'mike'{'">  mike . </div> '} </li>
                                                <li> {' <div th:case="'}'sam'{'">  sam .</div> '} </li>
                                                <li> {' <div th:case="*">  ???. </div> '} #    </li>
                                            </li>
                                            <li> {'</div>'} </li>
                                        </div>
                                    </li> <br />

                                    <li> <h3 className="h3"> th:if (else    ,   ) </h3>
                                        <li> if,if else </li>
                                        <li> {' <div th:if="${}=='}''"{' th:text="${}"> </div> '} </li>
                                        <li> {' <div th:unless="${}=='}''"{' th:text="${}"> </div> '} </li>
                                        <div className="block4">
                                            <li> {' <div th:if="${user.name}=='}'mike'"{' th:text="${mike}"> </div> '} </li>
                                            <li> {' <div th:unless="${user.name}=='}'sam'"{' th:text="${sam}"> </div> '} </li>
                                            <li> {' <div th:unless="true" th:text="${user.name}"> </div> '} </li>
                                        </div>
                                    </li> <br />

                                    <li> <h3 className="h3"> th:each </h3>
                                        <li>    foreach   </li>
                                        <li> {' <div th:each=":${}"> </div> '}
                                            <li> {' <div th:text="${.}"> </div> '} </li>
                                        </li>
                                        <div className="block4">
                                            <li> {' <div th:each="user:${users}"> </div> '} <small> # {'List<User> users'} </small>
                                                <li> {' <div th:text="${user.name}"> </div> '} </li>
                                            </li>
                                        </div>
                                    </li> <br />

                                    <li> <h3 className="h3"> th:action (context-path        .) </h3>
                                        <li> th:action context-path    </li>
                                        <div className="block4">
                                            <li> {' <form th:action ="{@{/}}"> </form> '} </li>
                                        </div>
                                    </li> <br />

                                    <li> <h3 className="h3"> th:href </h3>
                                        <li> html a href . </li>
                                        <li> {' <a th:href="@{url }"> </a> '} </li>
                                        <div className="block4">
                                            <li> {' <a th:href="@{http://www.naver.com}"> </a> '} </li>
                                            <li> {' <a th:href="@{/naver/news}"> </a> '} </li>
                                            <li> {' <a th:href="@{/naver/news(id=1,name='}'test'{')}"> </a> '} </li>
                                            <li> {' <a th:href="@{/naver/news(id=${user.id})}"> </a> '} </li>
                                        </div>
                                    </li> <br />

                                    <li> <h3 className="h3"> th:attr ( ) </h3>
                                        <li>  </li>
                                        <div className="block4">
                                            <li> </li>
                                        </div>
                                    </li> <br />

                                    <li> <h3 className="h3"> th:object </h3>
                                        <li> form  submit    </li>
                                        <li>    ,       . </li>
                                        <li> {'<form action="@{ }" th:object="${    }"> method="post" '} </li>
                                        <li> {' </form>'} </li>
                                        <div className="block4">
                                            <li> {'<form action="@{/userlogin}" th:object="${user}"> method="post" '}
                                                <li> {' <input type="text" th:field="*{id}" /> '} <small> # *      </small> </li>
                                                <li> {' <input type="password" th:field="*{password}" /> '} <small> # {' *{password}  ${user.password}  '} </small> </li>
                                            </li>
                                            <li> {' </form>'} </li>
                                        </div>
                                    </li> <br />

                                    <li> <h3 className="h3"> th:fragement th:insert th:replace ()  </h3>
                                        <li> th:fragement :   html   </li>
                                        <li> th:insert : th:fragement        </li>
                                        <li> th:insert : th:fragement        </li>
                                        <li> {' <div th:fragment="fragment">  </div> '} </li> <small> #  div      </small>
                                        <li> {' <div th:insert="~{/common :: fragment}"> </div>'} </li>
                                        <div className="block4">
                                            <li>  <small> templates/common.html div   </small>
                                                <li> {' <div th:fragment="fragment"> <span> div  span . </span> </div> '} </li>
                                            </li> <br />
                                            <li> <small> common.html    </small>
                                                <li> {'<div th:insert="~{/common :: fragment}"></div>'}  </li>
                                                <li> {'<div th:replace="~{/common :: fragment}"></div>'}  </li>
                                            </li>
                                        </div>
                                    </li> <br />

                                    <li> <h3 className="h3"> th:classappend (   ) </h3>
                                        <li> html      . </li>
                                        <li> {' <div th:classappend="'}' '{'"> </div> '} </li>
                                        <li>   : 'disabled' ,  </li>
                                        <div className="block4">
                                            <li> {' <div th:classappend="${currentPage} == 0 ? '}'disabled'{'"> </div> '} </li>
                                        </div>
                                    </li> <br />

                                    <li> <h3 className="h3"> th:errors (   ) </h3>
                                        <li>          ,  get  error    </li>
                                        <li> {' <div th:errors="${}"> </div> '} </li>
                                        <div className="block4">
                                            <li> {' <div th:errors="${user}"> </div> '} </li>
                                        </div>
                                    </li> <br />

                                </div>
                            </li>

                        </div>
                    </details>
                </div>

                <div className='block1'>
                    <details>
                        <summary> thymeleaf  spring security </summary>
                        <div className='block2'>

                            <h2 className="h2">  thymeleaf sec  </h2>
                            <li> sec: 
                                <div className="block3">
                                    <li> {'<html lang="UTF-8" xmlns:th="http://www.thymeleaf.org"  xmlns:sec="http://www.thymeleaf.org/extras/spring-security">'} <small> # html   </small> </li>
                                    <li> implementation 'org.thymeleaf.extras:thymeleaf-extras-springsecurity5' <small> # build.gradle dependencies  </small> </li>
                                </div>
                            </li>

                            <h2 className="h2">   {'&'} </h2>
                            <div className="block3">

                                <li> <h3 className="h3"> sec:authorize ( ) </h3>
                                    <li>      . </li>
                                    <div className='block4'>
                                        <li> {' <div sec:authorize="isAnonymous()"> </div> '} </li>
                                        <li> {' <div sec:authorize="isAuthenticated()"> </div> '} </li>
                                        <li> {' <div sec:authorize="!isAuthenticated()"> </div> '} </li>
                                        <li> {' <div sec:authorize="hasRole('}'ROLE_USER'{')"> </div> '} </li>
                                        <li> {' <div sec:authorize="!hasRole('}'ROLE_USER'{')"> </div> '} </li>
                                        <li> {' <div sec:authorize="hasAnyRole('}'ROLE_USER','ROLE_ADMIN'{')"> </div> '} </li>
                                    </div>
                                </li> <br />

                                <li> <h3 className="h3"> sec:authentication (, jsp   spring      !!) </h3>
                                    <li>  </li>
                                    <li> {' <div class="nav-item" sec:authorize="isAuthenticated()"> '}
                                        <li> {' <span sec:authentication="name">  </span> '} <small>    </small> </li>
                                        <li> {' <span sec:authentication property="principal" var="">  </span> '} </li>
                                        <li> {' <span sec:authentication property="principal.authorities" var="">  </span> '} <small> #   </small> </li>
                                        <li> {' <span sec:authentication property="principal.username" var="">  </span> '} <small> #   </small> </li>
                                        <li> {' <span sec:authentication property="principal.password" var="">  </span> '} </li>
                                        <li> {' <span sec:authentication property="principal.email" var="">  </span> '} </li>
                                        <li> {' <span sec:authentication property="principal.enabled" var="">  </span> '} </li>
                                        <li> {' <span sec:authentication property="principal.accountNonExpired" var="">  </span> '} </li>
                                        <li>  </li>
                                    </li>
                                    <li> {' </div> '} </li>
                                    <div className='block4'>
                                        <li> </li>
                                    </div>
                                </li> <br />

                                <li> <h3 className="h3"> #authorization (    )</h3>
                                    <li> sec:authentication            </li>
                                    <li>        </li>
                                    <li>       authorization.getAuthentication() != null   . </li>
                                    <li> {' <div th:text="${#authorization.getAuthentication()}"> </div> '} <small> # authorization   </small> </li>
                                    <li> {' <div th:text="${#authorization.getAuthentication().getPrincipal()}"> </div> '} <small> #     </small> </li>
                                    <li> {' <div th:text="${#authorization.expression('}'isAuthenticated()'{')}" }>  </div> '} <small> # true  </small>  </li>
                                    <div className='block4'>
                                        <li> {' <div th:if="${#authorization.getAuthentication().getPrincipal().getUsername() == board.client.username} </div> '} </li>
                                    </div>
                                </li> <br />

                            </div>

                        </div>
                    </details>
                </div>

            </div>
        </>
    );
}

export default SpringThymeleaf;