import React from 'react';
import { Link } from 'react-router-dom';

const LinuxList = () => {

    return (
        <div className="nav-menu2__inner-box">
            <div className="menu menu1">
                <Link to="/linux_basic">   </Link>
                <Link to="/linux_command">   </Link>
                <Link to="/linux_directory">    </Link>
                <Link to="/linux_shellscript">    </Link>
                <Link to="/linux_server">   </Link>
                <Link to="/linux_programinstall">    </Link>
            </div>
            <div className="menu menu2">
                <Link to="/linux_ubuntu">   </Link>
                <Link to="/linux_centos7">  CentOS7 </Link>
                <Link to="/linux_mobaxterm">  MobaXterm </Link>
                <Link to="/linux_vimeditor">  VimEditor </Link>
                <Link to="/linux_shell">   </Link>
            </div>
        </div>
    );
}

export default LinuxList;