import React from 'react';
import { Link } from 'react-router-dom';

const AlgorithmList = () => {


    return (
        <div className="nav-menu2__inner-box">
            <div className="menu menu1">
                <Link to="/algorithm_basic">   </Link>
                <Link to="/algorithm_backtracking">  </Link>
                <Link to="/algorithm_divide_conquer">   </Link>
                <Link to="/algorithm_dynamic">   </Link>
                <Link to="/algorithm_graph">   </Link>
                <Link to="/algorithm_greedy">   </Link>
            </div>
            <div className="menu menu2">
                <Link to="/algorithm_hash">   </Link>
                <Link to="/algorithm_recursive">   </Link>
                <Link to="/algorithm_search">   </Link>
                <Link to="/algorithm_sort">   </Link>
            </div>
        </div>
    );
}

export default AlgorithmList;